<script>
  import {onMount} from 'svelte'
  import store from 'store2'
  import {site,settings as settingsStore} from '@stores/data'

  export let multiple = false
  export let pages = null
  export let id = null
  export let html = ''
  export let css = ''
  export let js = ''
  export let settings = null
  export let dependencies = null
  export let loading = false;

  export let includeParentStyles = false

  $: if (includeParentStyles) {
    css = $site.styles.final + $settingsStore.globalStyles.compiled + css
  }

  var bc = new BroadcastChannel('preview');

  let ready = false
  bc.onmessage = () => {
    ready = true
  }

  $: multiple && ready && bc.postMessage(pages)

  $: store.set('preview', { 
    id,
    html,
    css,
    js,
    settings,
    dependencies
  })

  let iframe

  let timeout
  let cachedJs = js // reload the iframe whenever js changes to wipe the page's memory
  $: if (cachedJs !== js) {
    clearTimeout(timeout)
    timeout = setTimeout(() => {
      iframe.contentWindow.location.reload()
    }, 1000)
  }

</script>

<div class="h-full flex flex-col pl-2">
  <div class="preview-container flex-1" class:loading>
    <iframe title="Preview HTML" src="/?preview={multiple ? 'multiple' : true}" class="bg-white w-full h-full" bind:this={iframe}></iframe>
  </div>
  <a target="blank" class="preview-html" href="/?preview={multiple ? 'multiple' : true}">
    <span>open in separate tab</span>
    <span class="icon">
      <i class="fas fa-external-link-alt"></i>
    </span>
  </a>
</div>

<style>.preview-container {
  will-change: border-color;
  border-width: 4px;
  border-style: solid;
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 500ms;
  border-bottom: 0;
}

.preview-container.loading {
  border-color: rgb(248,68,73);
}

.preview-container.loading + .preview-html {
  --bg-opacity: 1;
  background-color: rgb(248,68,73);
  background-color: rgba(248, 68, 73, var(--bg-opacity));
  --text-opacity: 1;
  color: #fff;
  color: rgba(255, 255, 255, var(--text-opacity));
}

.preview-html {
  width: 100%;
  --bg-opacity: 1;
  background-color: #edf2f7;
  background-color: rgba(237, 242, 247, var(--bg-opacity));
  --text-opacity: 1;
  color: #718096;
  color: rgba(113, 128, 150, var(--text-opacity));
  font-size: 0.75rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  border-radius: 0.25rem;
  display: block;
  text-align: center;
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 500ms;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.preview-html:hover {
  --bg-opacity: 1;
  background-color: #cbd5e0;
  background-color: rgba(203, 213, 224, var(--bg-opacity));
}</style>