<script lang="ts">import { fade } from 'svelte/transition';
;
export let tabs;
export let activeTab;
export let variants = '';
export let activeColor = 'rgb(248,68,73)';
const tabStyles = (active) => active ? `border-color:${activeColor};color:${activeColor};` : `border-color:transparent;`;
</script>

{#if tabs.length > 1}
  <div class="tabs {variants}" transition:fade={{ duration: 200 }}>
    <ul>
      {#each tabs as tab}
        <li style={tabStyles(activeTab === tab)}>
          <button on:click={() => activeTab = tab}>
            {#if tab.icon}<i class="fas fa-{tab.icon}"></i>{/if}
            { typeof tab === 'string' ? tab : tab.label }
          </button>
        </li>
      {/each}
    </ul>
  </div>
{/if}


