<script>
  import {IconButton} from '@components/buttons'
  import {fade} from 'svelte/transition'
  import { createEventDispatcher, onMount } from 'svelte';
  const dispatch = createEventDispatcher();
  import {saveSymbolToDomain} from '@fb/firestore/domains'
  import {getComponentPreviewCode} from 'utils'

  import {settings,site,tailwind} from '@stores/data'
  let {styles} = $site
  let {globalStyles} = $settings;

  export let component;
  export let title = component.title || '';

  let editingTitle = false
  let titleInput

  function changeTitle() {
    editingTitle = false
    if (title !== component.title) {
      saveSymbolToDomain({ ...component, title })
    }
  }

  function saveComponentHeight(newIframeHeight) {
    if (newIframeHeight !== component.height) {
      saveSymbolToDomain({ ...component, height: newIframeHeight })
    }
  }

  let iframe
  let iframeHeight = component.height || 250

  onMount(() => {
    iframe.onload = () => {
      iframeHeight = iframe.contentWindow.document.body.scrollHeight
      saveComponentHeight(iframeHeight)
    }
  })

  const parentStyles = $tailwind + styles.final + globalStyles.compiled
  const previewCode = getComponentPreviewCode(component, parentStyles) 

</script>

<article class="message component-wrapper" in:fade={{ delay: 250, duration: 200 }}>
  <form on:submit|preventDefault={changeTitle}>
    <input type="text" bind:this={titleInput} bind:value={title} on:blur={changeTitle} on:focus={() => editingTitle = true}/>
  </form>
  <div class="message-header">
    <p class="component-label" on:click={() => titleInput.focus()} class:editing={editingTitle}>{title}</p>
    <div class="buttons">
      <IconButton variants="is-small is-light" icon="trash" on:click={() => dispatch('delete')}/>  
      <IconButton variants="is-small is-light" icon="edit" on:click={() => dispatch('edit')}/>  
      <IconButton variants="is-small is-link" icon="plus-circle" on:click={() => dispatch('select')}/>  
    </div>
  </div>
  <div class="message-body">
    <iframe class="w-full shadow-lg" style="height:{iframeHeight}px" bind:this={iframe} title="component preview" srcdoc={previewCode}></iframe>
  </div>
</article>

<style>iframe {
  width: 100%;
}

.component-wrapper {
  position: relative;
}

.component-label {
  display: block;
  min-width: 3rem;
  height: 1.5rem;
}

.component-label:before {
  content: '';
  display: inline-block;
  height: 1rem;
  width: 0;
  margin-right: 0;
  transition: margin-right 0.25s, width 0.25s;
  background: gainsboro;
}

input {
  user-select: none;
  position: absolute;
  opacity: 0;
}

.editing:before {
  content: '';
  width: 4px;
  margin-right: 5px;
  transition: margin-right 0.25s, width 0.25s;
}

