<script>
  import {createEventDispatcher} from 'svelte'
  const dispatch = createEventDispatcher();

  export let id = null
  export let label
  export let options
  export let selection = options[0]
  export let variants = ''

  $: dispatch('select', selection)


</script>

<div class={variants} {id}>
  <span class="text-base text-gray-900 font-semibold">{label}</span>
  <div class="toggle">
    {#each options as option}
      <button class:selected={selection === option} type="button" on:click={() => selection = option}>{option}</button>
    {/each}
  </div>
</div>

<style>.toggle {
  display: flex;
  margin-top: 0.25rem;
  border-radius: 0.125rem;
  overflow: hidden;
}

.toggle > button {
  flex: 1 1 0%;
  --bg-opacity: 1;
  background-color: #f7fafc;
  background-color: rgba(247, 250, 252, var(--bg-opacity));
  --text-opacity: 1;
  color: #4a5568;
  color: rgba(74, 85, 104, var(--text-opacity));
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-weight: 500;
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 200ms;
}

.toggle > button.selected {
  --bg-opacity: 1;
  background-color: #2d3748;
  background-color: rgba(45, 55, 72, var(--bg-opacity));
  --text-opacity: 1;
  color: #f7fafc;
  color: rgba(247, 250, 252, var(--text-opacity));
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 200ms;
  outline: 0;
}</style>